.App {
  background-image: url(https://images.unsplash.com/photo-1539350753666-b8667c6c1bce);
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center bottom;

  .wrapper {
    background-color: rgba(0, 0, 0, 0.5);
    min-height: 100vh;
    font-size: calc(10px + 2vmin);
    color: white;
    display: flex;
    flex-direction: column;
    justify-content: space-between;

    .header {
      padding: 3rem;
      display: flex;
      justify-content: space-between;

      .name {
        font-size: 2rem;
      }

      .menu {
        font-size: 1rem;
        
        a {
          color: white;
          text-decoration: none;

          &:hover {
            text-decoration: underline;
          }

          & + a {
            margin-left: 1rem;
          }
        }
      }
    }

    .content {
      max-width: 1400px;
      margin: 0 auto;

      h1 {
        font-size: 5rem;
        text-decoration: underline;
        opacity: 0.5;
        margin-top: 0;
        margin-bottom: 2rem;
      }

      p {
        padding-left: 3rem;
        font-size: 1.2rem;
      }
    }

    .footer {
      padding: 3rem;
      display: flex;
      justify-content: space-between;
      font-size: 1rem;

      .contact {
        span + span {
          margin-left: 0.75rem;
        }
      }
    }
  }
}
